export const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    /**
     * Append genitive case
     */
    const words = result.split(" ");
    const lastword = words.pop();
    result = words.join(" ");
    switch (lastword) {
      case "ÑÐµÐºÑƒÐ½Ð´":
        result += " ÑÐµÐºÑƒÐ½Ð´Ð¸Ð¹Ð½";
        break;
      case "Ð¼Ð¸Ð½ÑƒÑ‚":
        result += " Ð¼Ð¸Ð½ÑƒÑ‚Ñ‹Ð½";
        break;
      case "Ñ†Ð°Ð³":
        result += " Ñ†Ð°Ð³Ð¸Ð¹Ð½";
        break;
      case "Ó©Ð´Ó©Ñ€":
        result += " Ó©Ð´Ñ€Ð¸Ð¹Ð½";
        break;
      case "ÑÐ°Ñ€":
        result += " ÑÐ°Ñ€Ñ‹Ð½";
        break;
      case "Ð¶Ð¸Ð»":
        result += " Ð¶Ð¸Ð»Ð¸Ð¹Ð½";
        break;
      case "Ñ…Ð¾Ð½Ð¾Ð³":
        result += " Ñ…Ð¾Ð½Ð¾Ð³Ð¸Ð¹Ð½";
        break;
      case "Ð³Ð°Ñ€Ð°Ð½":
        result += " Ð³Ð°Ñ€Ð°Ð½Ñ‹";
        break;
      case "Ñ…Ò¯Ñ€ÑÑ…Ð³Ò¯Ð¹":
        result += " Ñ…Ò¯Ñ€ÑÑ…Ð³Ò¯Ð¹ Ñ…ÑƒÐ³Ð°Ñ†Ð°Ð°Ð½Ñ‹";
        break;
      default:
        result += lastword + "-Ð½";
    }

    if (options.comparison && options.comparison > 0) {
      return result + " Ð´Ð°Ñ€Ð°Ð°";
    } else {
      return result + " Ó©Ð¼Ð½Ó©";
    }
  }

  return result;
};
